@media (min-width: 1170px) and (max-width: 1365px) {
    .help-icon {
        float: none;
        margin: auto auto 20px;
        text-align: center;
    }
    .help-text {
        float: none;
        text-align: center;
        width: 100%;
    }
    .count-down .table-cell {
        float: left;
        margin: 0 29px;
        text-align: center;
    }
    .countdown-text {
        float: none;
    }
    .nivo-prevNav .left {
        margin-left: 60px;
    }
    .nivo-nextNav .right {
        margin-right: 60px;
    }
    .main-menu ul li {
        padding: 47px 21px;
    }
    .stick .main-menu ul li {
        padding: 18px 21px;
    }
}


/* Normal desktop :992px. */

@media (min-width: 992px) and (max-width: 1169px) {
    h1.title1 {
        font-size: 35px;
    }
    .nivo-nextNav .right {
        margin-right: 60px;
    }
    .nivo-prevNav .left {
        margin-left: 60px;
    }
    .help-text {
        text-align: center;
        width: 100%;
    }
    .help-icon {
        margin: auto auto 20px;
        float: none;
    }
    .countdown-text {
        width: 240px;
    }
    .count-down .table-cell {
        margin: 0 28px;
    }
    .countdown-side-area {
        margin-top: 17px;
    }
    .people-say-slide .owl-prev {
        left: 0;
    }
    .people-say-slide .owl-next {
        right: 0;
    }
    .couses-button .btn-md {
        display: block;
        margin: 10px auto auto;
        width: 140px;
    }
    .main-menu ul li {
        padding: 47px 20px;
    }
    .nivo-caption {
        top: 28%;
    }
    .stick .main-menu ul li {
        padding: 18px 20px;
    }
    .donate-list .box {
        width: 115px;
        margin-right: 10px;
    }
    .quick-donation-section .donate-list label .check-icon::before {
        left: -30px;
    }
    .quick-donation-section input[type="radio"]+label .check-icon::after {
        left: -29px;
    }
}


