/* small mobile :320px. */

@media (max-width: 767px) {
    .nivo-caption {
        width: 100%;
        top: 23%;
        left: 10%;
    }
    h1.title1 {
        font-size: 18px;
    }
    .slider-button-area a {
        margin-right: 5px;
        font-size: 10px;
        padding: 8px 7px;
    }
