    }
    /* about page css */

    .single-project {
        margin-bottom: 30px;
    }
    .about-skill .bar {
        float: left;
        text-align: center;
        width: 100%;
    }
    .call-to-action {
        float: none;
    }
    .blog {
        overflow: hidden;
    }
    .blog-pagination {
        float: left;
        margin-top: 15px;
    }
    .single-box {
        margin-bottom: 30px;
    }
    .couses-page-content .couses-button .btn-md {
        display: block;
        margin: 10px auto auto;
        width: 140px;
    }
    .event-text>h3 {
        margin-top: 15px;
    }
    .post-thum img {
        width: inherit;
    }
    .mobile-menu-area {
        display: block;
    }
    .slider-button-area a {
        display: block;
        margin-bottom: 15px;
        margin-right: 15px;
    }
    .youtube-area.height-100vh {
        height: 500px;
    }
    .youtube-text {
        padding: 80px 0;
        width: 80%;
    }
    .form-fields {
        width: 100%;
    }
    .form-fields>input {
        width: 100%;
        margin-bottom: 15px;
    }
    .flicker-widget li {
        width: 29.5%;
    }
    .main-menu {
        display: none;
    }
    .stick {
        position: inherit;
    }
    .mb-90 {
