    }
    .quick-donation-section .enter-amount {
        margin-right: 0;
        margin-bottom: 15px;
    }
    .quick-donation-section .donate-btn button {
        padding: 0 57px;
    }
    .enter-amount {
        float: left;
        width: 100%;
    }
    .donate-list .box {
        width: 195px;
    }
    .quick-donation-section .enter-amount {
        width: 100%;
    }
}


